import React from 'react';

const Menu = ({catagorySelect}) => {
return(
	<div className='flex flex-column br black-80 ml1 ml2-ns ml5-m ml7-l mt0 fixed w-15-m w-10-l '>
 <h1 className='f3 b dn db-l'> Categories</h1>
 <p className='dn db-l grow pointer' onClick={()=>catagorySelect("Stupid Section")}>Stupid Section</p>
 <p className='dn db-l grow pointer' onClick={()=>catagorySelect("Technical")}>Technical</p>
 <p className='dn db-l grow pointer' onClick={()=>catagorySelect("Non-Technical")}>Non-Technical</p>
 <p className='dn db-l grow pointer' onClick={()=>catagorySelect("Confession")}>Confession</p>
 <p className='dn db-l grow pointer' onClick={()=>catagorySelect("interview")}>Interview</p>
  <p className='dn db-l grow pointer' onClick={()=>catagorySelect("Not Specified")}>Not Specified</p>
	</div>
	)
	}
export default Menu;