import React from 'react';
import Dot from './delete.jpg';
import m from '../male.png';
import f from '../female.png';
import TimeAgo from 'react-timeago';
import Switch from "react-switch";
import dislike from './notlike.png';
import like from './liked.png';
import Aoc from '../../Aoc/aoc';
import ReadMoreAndLess from 'react-read-more-less';
import './boxlist.css'



const Boxlist=(props)=>{
	const {onAnswerLike,qued,qfile,showlike, ql, scatagory,selectUser,k,index,qshow, ShowCall, question, qemail, qj, answer, id, qname, qyear, qbranch, qgender, onAnswerChange, onSubmitAnswer, route, r, deleteAnswer, handleChange, checked, user, show, toShow, deleteQuestion, showq, toShowq, catagory}=props;
	var count=0
	var alike=0
	var que=question;
	que=que.replace("code*","<pre>")
	que=que.replace("*code","</pre>")
return(
<div className=' black-80  w-100 w-50-m w-40-l bg-white mt2 mt4-m mt0 mr-3 center-l h-50 center-m  shadow-5 overflow-hidden' key={k}>
	<p className='i ml2 mt1 mb0 f6 fl clr pointer w-100' onClick={()=>{selectUser(qemail,qname)}}>
{qued.pic!==null?<img src={`https://easystudies.herokuapp.com/${qued.pic}`} height='40em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}
	{qname.toUpperCase()}, {qbranch}, {qyear}, {catagory}, {scatagory} </p>
	<h1 className='b w-100 f5 f4-ns Helvetica Neue f3-m f4-l ma2 pr3 pb3 fl bb b--light-silver'>Q. {que} ? 
		<br/>
	<div key={k+9}>{qfile!==null?<img src={`https://easystudies.herokuapp.com/${qfile}`} className='center h4 h5-l h5-m pa2' height='260'/>:null}</div>
     <p className='ma0 pa0 pt2 f7 black-40 pointer' key={k+1}>{ql} likes</p>
     <div className='pt1'>
     {route==='HOME'?<div key={k+3}>{showlike===true?<img key={k+32} src={like}
           height='40em '
           className='fl grow pointer'
          onClick={()=>{props.qlike(id,user.email,index)}}/>:<img key={k+31} src={dislike}
           height='40em '
           className='fl grow pointer'
          onClick={()=>{props.qlike(id,user.email,index)}}
          /> }</div>:null}<TimeAgo date={qj}  className='f7 black-40 fr pr3'/></div>
    
			{route==='HOME'&&qemail===user.email?
									<div><img key={Math.random()} alt='' className='relative grow fr pr2 pl4 pointer' onClick={()=>toShowq('inline-block')} src={Dot}  height='20' width='20'/>					
						<div style={showq} key={Math.random()} className='ta ba absolute bg-light-gray f7 fr ml7-l mt7-m ml6 mt v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
						<h className='pb1'>Do you want to delete the question?</h>				<br/>	<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteQuestion(id)}>	Delete</button><br/>
						<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>toShowq('none')}>	Cancel</button></div>
						</div>	:<p/>}</h1>
						{answer.map((use,i)=>{
						if(answer[i].qid===id){
								count++
							}
						})}
{qshow===true?<div className=' flex flex-column fl w-100 mt0 mb0 pt0 pb0' >
		<button class="f6 link grow ba w4 ph3 ml1 ml2-m ml3-l  pv2 mb2 dib 	mid-gray" onClick={()=>{ShowCall(index,false)}}>Hide replies</button>
	{answer.map((use,i)=>{

		if(answer[i].name==='Anonymous'){
		answer[i].branch='';
		answer[i].year='';
		answer[i].pic=null;
	}
	alike=answer[i].like	
		if(answer[i].qid===id){
			
		return(<div key={Math.random()} className='answer ma2 flex flex-column w-100 black-50 mt0 mb0' >
			
			<p className='ma0 pa0'><span key={Math.random()} className='pointer clr ml0 mb0 pb0 i fl f7 f6-m f6-l' onClick={()=>{selectUser(answer[i].email,answer[i].name)}}>{answer[i].pic!==null?<img src={`https://easystudies.herokuapp.com/${answer[i].pic}`} height='40em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}{answer[i].name.toUpperCase()}, {answer[i].branch}, {answer[i].year} 
			</span>
<TimeAgo date={answer[i].joined}  className='f7 black-40 mb0 mt0 pb0 pt1 pr3 fr'/>
 {route==='HOME'?<span className='fr pa1 grow pr2 pointer' onClick={()=>{onAnswerLike(answer[i].id,user.email,i)}}> {answer[i].like} Upvotes</span>
 			:null}		</p>
			 			 
			<span key={Math.random()} className='fl black ml2 f6 f5-m f5-l pr3 ma0 pa0 '>
			<p className='w-100 pt0 mt1 b black-90'>
			{console.log(answer[i].answer)}
			{answer[i].answer} </p></span>
         
			
			{route==='HOME'&&answer[i].email===user.email?
									<div className='mt0 mb0 pt0 pb0'><img key={Math.random()} alt='' className='relative grow fl pl4 pointer mt0 mb0 pt0 pb0' onClick={()=>toShow('inline-block')} src={Dot}  height='20' width='20'/>
							<div style={show} key={Math.random()} className='ta ba absolute f7 fl bg-light-gray  v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
							<h className='pb1'>Do you want to delete the answer?</h>				<br/>	
									<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteAnswer(answer[i].qid,answer[i].answer)}>	Delete</button><br/>
						<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>toShow('none')}>	Cancel</button></div>
						</div>	:null}
						<p className='mt0 mb0 ml5 bb b-black-20 pb0 pt0'></p>
			</div>
			)}else{}
	})}
				{count===0?
											<p className='tc l mt0 mb0 pt0 pb0'>No replies yet!!</p>
											:<p></p>
											}			


	</div>:<div><button className="f6 link grow ba ph3 ml1 ml2-m ml3-l pv2 mb2 dib moon-gray bg-dark-gray" onClick={()=>{ShowCall(index,true)}}>{count} Replies to Show</button><br/></div>}

		<br className='mt0 mb0 pt0 pb0'/>
		{route==='HOME'&&user.verified===true?
		<div className='dib w-100'>
		
			<textarea
			placeholder='Answer'
			 className='fl dib border-box hover-black  measure ba b--black-20 pa2  br2 mb2 h2 w-70 w-40-ns w-50-m w-60-l pa2  ba bg-transparent hover-bg-black hover-white  ml3' 
			onChange={onAnswerChange}/>
			<button className='fl dib bg-near-black br1 v-top w-20 h2 pr2 white f6 grow shadow-5 bn' onClick={()=> onSubmitAnswer(id)}>Reply</button>
			
		</div>	
	:null
}</div>
)
}

export default Boxlist;