import React from 'react';

const Form=({load, Loading,changingRoute, OnEmailChange, OnPasswordChange, insign, Onsubmitsignin})=>{
return(
<article className="br3 tc bg-white ba black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">
<main className="pa4 black-80">
		<div className="measure center">
		<Loading color="bg-near-black" load={load} x='25%' y='25%'/>
		<fieldset id="sign_up" className="ba b--transparent ph0 mh0">
		<legend className="f1 fw6 ph0 mh0">Sign In</legend>
		<div className="mt3">
		<label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="email" 
		name="email-address"  
		id="email-address"
		onChange={OnEmailChange}
		/>
		</div>
		<div className="mv3">
		<label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
		<input 
		className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="password"
		name="password"  
		id="password"
		onChange={OnPasswordChange}
		/>
		</div>
		</fieldset>
		<div className="">
		<input className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" onClick={Onsubmitsignin} type="submit" value="Sign in"/>
		</div>
		<div className="lh-copy mt3">
		<p  onClick={() => insign('Register')} className="f6 link dim black db pointer">Register</p>
		<p  onClick={() => changingRoute('emailotp')} className="f6 link dim black db pointer">Forgot Password?</p>
		</div>
		</div>
</main>
</article>
)
}

export default Form;
