import React, { Component } from 'react';
import './App.css';
import Main from './components/Main/main';
import Home from './components/home/home';
import Particles from 'react-particles-js';

const initialstate={
        load:false,
        route:'main',
        menu:false,
      isSignout:false,
      userC:[{count:'0'}],
      user:{
        id:'',
       name:'a',
       email:'',
      joined: '',
      gender:'',
      number:'',
      verified:'',
      followers:'',
      following:''
        }
}
var count1=0;

class App extends Component {

  constructor(){
      super();
      this.state=initialstate
       }

    componentDidMount(){
      fetch('https://quiet-escarpment-25332.herokuapp.com/userc',{
      method:'get',
      headers:{'Content-Type':'application/json'},
    }).then(response=>response.json())
      .then(count=>{  
      this.setState({userC:count});   
    })
      if(localStorage.getItem('dataa')!==''){
this.oncall();
}
    }
  
oncall=()=>{
   const obj =JSON.parse(localStorage.getItem('dataa'));
   this.setState({user:obj})

 if(obj){
    this.getuser(obj);
    this.onsign('HOME');
    
  }
}

  getuser=(obj)=>{
    fetch('https://quiet-escarpment-25332.herokuapp.com/user',{
      method:'post',
      headers:{'Content-Type':'application/json'},
      body:JSON.stringify({
      email:obj.email
    })
    }).then(response=>response.json())
      .then(user=>
        {
          this.setState({user:user})

        })
  }

      userdetail=(data)=>{
      localStorage.setItem('dataa',JSON.stringify(data));
      this.setState({user:data})
    }

    onsign=(route)=>{
  this.setState({route:route});
  if(route==='main'){
     localStorage.setItem('dataa','');
     
  }
  }
      time=(a)=>{
      if(a){this.setState({load:true});}
      else{  this.setState({load:false});}
    }

MenuCall=(e)=>{
        this.setState({menu:e})    
      
}

    menuCall=()=>{
      count1++;
      if(count1%2!==0){
        this.setState({menu:true})
      }
      else{this.setState({menu:false})}
    }


  render() {
    const{load,route,user, menu, isCalled, isCorY, calld, userC}=this.state;

    return (
      <div className='bg-light-gray' >
           {route==='main'?
           <Main onsign={this.onsign} MenuCall={this.MenuCall}  menu={menu} userC={userC} isCalled={isCalled} isCorY={isCorY} calld={calld} catagorySelect={this.catagorySelect} yearSelect={this.yearSelect} IsCalled={this.IsCalled} menuCall={this.menuCall} route={route} time={this.time} load={load} userd={this.userdetail} user={user}/>
                          
                         : (route==='HOME'?
                                   <Home user={user} MenuCall={this.MenuCall} userC={userC} isCalled={isCalled} isCorY={isCorY} calld={calld} catagorySelect={this.catagorySelect} yearSelect={this.yearSelect} IsCalled={this.IsCalled} menu={menu} menuCall={this.menuCall} time={this.time} load={load} route={route} onsign={this.onsign}/>
                                   :<p/>)
      
                 }

 </div>

            
    
    );
  }
}

export default App;
