import React from 'react';

const Year = ({yearSelect}) => {
	
return(
	<div className=' flex flex-column bl fr black-80 fixed right-0 mr7 mt0 pl4 w-15-m w-10-l '>
 <h1 className='f3 b dn db-l'> Years </h1>
 <p className='dn db-l grow pointer' value='1' onClick={()=>yearSelect("1")}>First Year</p>
 <p className='dn db-l grow pointer' value='2' onClick={()=>yearSelect("2")}>Second Year</p>
 <p className='dn db-l grow pointer' value='3' onClick={()=>yearSelect("3")}>Third Year</p>
 <p className='dn db-l grow pointer' value='4' onClick={()=>yearSelect("4")}>Fourth Year</p>
	
</div>
	)
	}
export default Year;